//promisepromisepromisereact
//suspense promise
export const wrapPromise = (promise) => {
  let status = "pending";
  let result;

  let suspender = promise
    .then((r) => {
      status = "success";
      result = r;
    })
    .catch((e) => {
      status = "error";
      result = e;
    });

  return {
    read: () => {
      if (status === "pending") {
        throw suspender; //   Suspense  Promise 
      } else if (status === "error") {
        throw result; //  
      }
      return result; //  
    },
  };
};
